   /* Navbar Style */
   .navbar-light {
       transition: .6s;
       -webkit-transition: .6s;
       /*#e9dbc8*/
       background-color: #6666ff !important;
   }
   .navbar-light .navbar-nav .nav-link {
       color: #000;
       padding: 15px 15px;
       text-transform: uppercase;
       cursor: pointer;
   }
   .navbar-light .navbar-nav .nav-link:hover {
       color: #fff;
   }
   .navbar-light .navbar-nav .active>.nav-link, .navbar-light .navbar-nav .nav-link.active, .navbar-light .navbar-nav .nav-link.show, .navbar-light .navbar-nav .show>.nav-link {
       color: #fff !important;
       background: none;
   }
   .navbar-light.fixed-top, .search-modal, .home-video-area::before, .owl-thumb-item.active .slide-service-item, .owl-thumb-item:hover .slide-service-item {
       color: #fff;
   }
   .fixed-top {
       z-index: 99 !important;
   }
   .navbar-light.fixed-top, .search-modal, .home-video-area::before, .owl-thumb-item.active .slide-service-item, .owl-thumb-item:hover .slide-service-item {
       color: #fff !important;
   }
   .navbar-light .navbar-brand {
       position: absolute;
       top: -50px;
       z-index: 99999;
       padding: 0;
       right: 0;
   }
   .navbar-brand img {
       cursor: pointer;
   }
   .menu-shrink {
       box-shadow: 0 0 10px #ebebeb;
   }
   .menu-shrink .navbar-nav .nav-link {
       color: #fff !important;
   }
   .menu-shrink .navbar-brand.logo-2{
     display: block;
     top: 8px;
   }
   .navbar-brand.logo-2, .menu-shrink .navbar-brand.logo{
     display: none;
   }
