/* ========================= 
    Default CSS
   ========================= */
   html, body {
       font-family: 'Poppins', sans-serif;
       font-size: 14px;
       font-weight: 400;
       color: #848484;
       line-height: 25px;
       letter-spacing: .5;
       overflow-x: hidden;
       -webkit-font-smoothing: antialiased;
       direction: rtl;
   }
   .navbar{
       padding: 0;
   }
   a:hover, a:focus {
       text-decoration: none;
   }
   button:focus, input:focus {outline:0;}
   ul {
       list-style: none;
       padding: 0;
       margin: 0;
   }
   .list-inline li{
       display: inline-block;
       padding-right: 5px;
       padding-left: 5px;
   }
   h2 {
       font-weight: 500;
       font-size: 36px;
   }
   h3 {
       color: #626262;
       font-size: 22px;
       font-weight: 500;
   }
   p {
       line-height: 1.9;
   }
   .ptb-100 {
       padding: 100px 0;
   }
   .mrl-50 {
     margin-left: 50px;  
   }
   .diplay-table {
       display: table;
       width: 100%;
       height: 100%;
   }
   .display-table-cell {
       display: table-cell;
       vertical-align: middle;
   }
   .section-title h2 {
       margin: 0 0 25px;
       font-weight: 600;
       text-transform: capitalize;
   }
   .section-title {
       position: relative;
       z-index: 1;
   }
   .section-title-bg {
       color: #f0f0f0;
       font-size: 100px;
       line-height: 0;
       position: absolute;
       top: 0;
       left: 0;
       width: 100%;
       z-index: -1;
       opacity: 0.8;
       font-family: 'Monoton', cursive;
       text-transform: uppercase;
   }
   .button, .center-wrap button button {
       position: relative;
       display: block;
       background: none center center no-repeat;
       background-size: cover;
       text-transform: uppercase;
       letter-spacing: .5px;
       padding: 10px 4px;
       text-align: center;
       max-width: 180px;
       min-width: 170px;
       cursor: pointer;
       border-radius: 4px;
       overflow: hidden;
       -webkit-transition: border 1s cubic-bezier(0.19,1,.22,1),color .6s cubic-bezier(0.19,1,.22,1);
       -webkit-transition: border 1s cubic-bezier(0.19,1,.22,1), color .6s cubic-bezier(0.19,1,.22,1), background 5s cubic-bezier(0.19,1,.22,1);
       transition: border 1s cubic-bezier(0.19,1,.22,1), color .6s cubic-bezier(0.19,1,.22,1), background 5s cubic-bezier(0.19,1,.22,1);
   }
   .button a, .center-wrap button {
       color: #fff;
       text-decoration: none;
       font-size: 15px;
       font-weight: 500;
       background: transparent;
       border: transparent;
   }
   button {
       padding: 0;
   }
   .button .mask {
       background: #fff;
       background: rgba(255,255,255,0.5);
   }
   .button .mask {
       position: absolute;
       display: block;
       width: 200px;
       height: 100px;
       -webkit-transform: translate3d(-120%,-50px,0) rotate3d(0,0,1,45deg);
       transform: translate3d(-120%,-50px,0) rotate3d(0,0,1,45deg);
       -webkit-transition: all 1.1s cubic-bezier(0.19,1,.22,1);
       transition: all 1.1s cubic-bezier(0.19,1,.22,1);
   }
   .button:hover {
       border-color: #fff;
   }
   .button:hover a {
       color: #fff;
   }
   .button:hover .mask {
       background: #fff;
       -webkit-transform: translate3d(120%,-100px,0) rotate3d(0,0,1,90deg);
       transform: translate3d(120%,-100px,0) rotate3d(0,0,1,90deg);
   }
   .btn-a {
       color: #fff;
       text-decoration: none;
       font-size: 15px;
       font-weight: 500;
       background: transparent;
       border: transparent;
   }
   .btn-a:hover {
       color: #fff;
   }
   .plan .btn-a{
       color: #272727;
   }
   .featured .btn-a {
       color: #fff !important;
   }
   .plan .button {
       background-color: transparent;
       margin: auto;
       margin-top: 40px;
   }
   .plan.featured .button {
       margin: auto;
       margin-top: 40px;
   }
   .plan.featured .button a{
       color: #fff;
   }
   .all-post .button {
       margin: 50px auto 0;
   }
   .all-post .button a {
       color: #fff;
   }
   .all-post .button a:hover {
       color: #fff;
   }
   /* ========================= 
       End Default CSS
      ========================= */
   
   /* ========================= 
       Preloader CSS
      ========================= */
   .site-preloader-wrap {
       background: #fe5619;
       height: 100%;
       position: fixed;
       width: 100%;
       z-index: 9999999;
   }
   .site-preloader-wrap .sk-cube-grid {
       left: 50%;
       margin: -20px 0 0 -20px;
       position: absolute;
       top: 50%;
   }
   .sk-cube-grid {
     width: 40px;
     height: 40px;
     margin: 100px auto;
   }
   .sk-cube-grid .sk-cube {
     width: 33%;
     height: 33%;
     background-color: #fff;
     float: left;
     -webkit-animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out;
             animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out; 
   }
   .sk-cube-grid .sk-cube1 {
     -webkit-animation-delay: 0.2s;
             animation-delay: 0.2s; }
   .sk-cube-grid .sk-cube2 {
     -webkit-animation-delay: 0.3s;
             animation-delay: 0.3s; }
   .sk-cube-grid .sk-cube3 {
     -webkit-animation-delay: 0.4s;
             animation-delay: 0.4s; }
   .sk-cube-grid .sk-cube4 {
     -webkit-animation-delay: 0.1s;
             animation-delay: 0.1s; }
   .sk-cube-grid .sk-cube5 {
     -webkit-animation-delay: 0.2s;
             animation-delay: 0.2s; }
   .sk-cube-grid .sk-cube6 {
     -webkit-animation-delay: 0.3s;
             animation-delay: 0.3s; }
   .sk-cube-grid .sk-cube7 {
     -webkit-animation-delay: 0s;
             animation-delay: 0s; }
   .sk-cube-grid .sk-cube8 {
     -webkit-animation-delay: 0.1s;
             animation-delay: 0.1s; }
   .sk-cube-grid .sk-cube9 {
     -webkit-animation-delay: 0.2s;
             animation-delay: 0.2s; }
   
   @-webkit-keyframes sk-cubeGridScaleDelay {
     0%, 70%, 100% {
       -webkit-transform: scale3D(1, 1, 1);
               transform: scale3D(1, 1, 1);
               -ms-transform: scale3D(1, 1, 1);
     } 35% {
       -webkit-transform: scale3D(0, 0, 1); 
               transform: scale3D(0, 0, 1); 
     }
   }
   
   @keyframes sk-cubeGridScaleDelay {
     0%, 70%, 100% {
       -webkit-transform: scale3D(1, 1, 1);
               transform: scale3D(1, 1, 1);
     } 35% {
       -webkit-transform: scale3D(0, 0, 1);
               transform: scale3D(0, 0, 1);
               -ms-transform: scale3D(0, 0, 1);
     } 
   }
   /* ========================= 
       End Preloader CSS
      ========================= */
   
