   /*-- Header Search --*/
   .navbar-light .container{
     position: relative;
   }
   .header-search {
       padding: 20px 10px;
       -webkit-transition: all 0.3s ease 0s;
       transition: all 0.3s ease 0s;
       position: absolute;
       left: -12px;
       top: -7px;
   }
   .header-search .search-toggle {
     background-color: transparent;
     border: medium none;
     color: #575757;
     display: block;
     padding: 0;
   }
   .menu-shrink .header-search .search-toggle {
       color: #fff;
   }
   .header-search .search-toggle i {
     display: block;
     font-size: 15px;
     line-height: 27px;
   }
   
   /*-- Search Modal --*/
   .search-modal {
     z-index: 9999999;
     cursor: url(data:image/png;base64,), auto;
   }
   .modal-dialog {
       position: absolute;
       top: 50%;
       width: 45%;
       max-width: unset;
       right: 0;
       left: 0;
       margin-top: -100px;
   }
   .modal-dialog .modal-content {
       background: transparent;
