   }
   .header-search-form form input {
     border: medium none;
     color: #fff;
     font-size: 45px;
       height: 60px;
     padding: 0 30px 0 0;
     width: 100%;
       background: transparent;
   }
   .header-search-form form button {
     background-color: transparent;
     border: medium none;
     color: #fff;
     height: 60px;
     position: absolute;
     right: 0;
     top: 0;
   }
   .header-search-form form button i {
     display: block;
