   }
   .work-details:hover img{ opacity: 0.5; }
   .work-details .box-content{
       padding: 30px 10px 30px 0;
       position: absolute;
       top: 0;
       left: 0;
       bottom: 0;
       right: 0;
       opacity: 0;
       transition: all 0.35s ease 0s;
       -webkit-transition: all 0.35s ease 0s;
   }
   .work-details:hover .box-content{
