  /*------------------------------------------------------------------
  [ 2 ]*/
  @font-face {
    font-family: Lato-Regular;
    src: url(../../static/media/Lato-Regular.7f690e50.ttf); 
  }
  
  
  /*//////////////////////////////////////////////////////////////////
  [ RS PLUGIN ]*/
  
  /*------------------------------------------------------------------
  [ Bootstrap ]*/
  .container {max-width: 1080px;}
  
  
  
  /*//////////////////////////////////////////////////////////////////
  [ LOADDING ]*/
  .animsition-loading-1 {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
  }
  
  .loader05 {
    width: 56px;
    height: 56px;
    border: 4px solid #17b978;
    border-radius: 50%;
    position: relative;
    -webkit-animation: loader-scale 1s ease-out infinite;
            animation: loader-scale 1s ease-out infinite;
    top: 50%;
    margin: -28px auto 0 auto; }
  
  @-webkit-keyframes loader-scale {
    0% {
      -webkit-transform: scale(0);
              transform: scale(0);
      opacity: 0; }
    50% {
      opacity: 1; }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
      opacity: 0; } 
  }
  
  @keyframes loader-scale {
    0% {
      -webkit-transform: scale(0);
              transform: scale(0);
      opacity: 0; }
    50% {
      opacity: 1; }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
      opacity: 0; } 
  }
  
  
  /*//////////////////////////////////////////////////////////////////
  [ BUTTON BACK TO TOP ]*/
  .btn-back-to-top {
    display: flex;
    position: fixed;
    width: 35px;
    height: 35px;
    bottom: -40px;
    right: 40px;
    background-color: #17b978;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    opacity: 0.6;
    cursor: pointer;
    transition: all 0.4s;
    -webkit-transition: all 0.4s;
    -o-transition: all 0.4s;
    -moz-transition: all 0.4s;
  }
  
  .symbol-btn-back-to-top {
    font-size: 18px;
    color: white;
    line-height: 1em;
  }
  
  .btn-back-to-top:hover {
    opacity: 1;
  }
  
  @media (max-width: 575px) {
    .btn-back-to-top {
      bottom: 0px;
      right: 15px;
    }
  }
  
  .show-btn-back-to-top {bottom: 0;}
  
  
  /*//////////////////////////////////////////////////////////////////
  [ Header ]*/
  
  /*>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>
  [ Header Desktop ]*/
  
  /*==================================================================
  [ Top-bar ]*/
  .topbar {
    background-color: #111;
  }
  
  .content-topbar {
    min-height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  /*---------------------------------------------*/
  .left-topbar {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin-left: -13px;
    margin-right: -13px;
  }
  
  .left-topbar-item {
    font-family: Roboto-Regular;
    font-size: 12px;
    color: #ccc;
    line-height: 1.8;
    padding: 0 13px;
    position: relative;
    transition: all 0.3s;
    -o-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -webkit-transition: all 0.3s;
  }
  
  a.left-topbar-item:hover {
    color: #17b978;
  }
  
  .left-topbar-item::before {
    content: "";
    display: block;
    position: absolute;
    width: 1px;
    height: 10px;
    background-color: #fff;
    opacity: 0.1;
    left: 0;
    top: calc(50% - 5px);
  }
  
  .left-topbar-item:first-child:before {
    display: none;
  }
  
  /*---------------------------------------------*/
  .right-topbar {
    margin-left: -4px;
    margin-right: -4px;
  }
  
  .right-topbar a {
    font-size: 18px;
    color: #ccc;
    line-height: 1.5;
    margin: 0 4px;
    transition: all 0.3s;
    -o-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -webkit-transition: all 0.3s;
  }
  
  .right-topbar a:hover {
    color: #17b978;
  }
  
  /*------------------------------------------------------------------
  [ Topbar mobile ]*/
  .topbar-mobile {
    background-color: #222;
    padding: 8px 25px 5px 25px;
  }
  
  .topbar-mobile li {
    padding: 5px 0;
  }
  
  .topbar-mobile .right-topbar,
  .topbar-mobile .left-topbar {
    justify-content: flex-start;
  }
  
  
  
  /*==================================================================
  [ Logo ]*/
  .wrap-logo {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 125px;
    padding-top: 25px;
    padding-bottom: 10px;
  }
  
  /*---------------------------------------------*/
  .logo {
    width: 25%;
    height: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  
  .logo img {
    max-width: 100%;
    max-height: 50%;
  }
  
  /*---------------------------------------------*/
  .banner-header {
    width: 70%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  
  .banner-header img {
    max-width: 100%;
    max-height: 100%;
  }
  
  /*---------------------------------------------*/
  .wrap-logo.no-banner {
    justify-content: center;
  }
  
  .wrap-logo.no-banner .logo {
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
  }
  
  /*---------------------------------------------*/
  .logo-stick {
    display: none;
    line-height: 0;
    margin-right: 15px;
  }
  
  .logo-stick img {
    max-width: 115px;
    max-height: 30px;
  }
  
  
  /*==================================================================
  [ Main nav ]*/
  .wrap-main-nav {
    width: 100%;
    height: 55px;
    z-index: 1000;
    position: relative;
  }
  
  .main-nav {
    width: 100%;
    height: 55px;
    background-color: #fff;
  
    box-shadow: 0 3px 8px 0px rgba(0,0,0,0.05);
    -moz-box-shadow: 0 3px 8px 0px rgba(0,0,0,0.05);
    -webkit-box-shadow: 0 3px 8px 0px rgba(0,0,0,0.05);
    -o-box-shadow: 0 3px 8px 0px rgba(0,0,0,0.05);
    -ms-box-shadow: 0 3px 8px 0px rgba(0,0,0,0.05);
  
    -webkit-transform: translateY(0%);
    transform: translateY(0%);
  }
  
  .menu-desktop {
    max-width: calc(100% - 30px);
    width: 1050px;
    margin: 0 auto;
    height: 55px;
    position: relative;
  }
  
  .main-menu {
    list-style-type: none;
    margin: 0;
    height: 100%;
    display: flex;
    align-items: center;
  }
  
  .main-menu > li {
    height: 100%;
    position: relative;
  }
  
  .main-menu > li > a {
    font-family: Roboto-Medium;
    font-size: 16px;
    line-height: 1.5;
    color: #222;
    
    position: relative;
    display: flex;
    align-items: center;
    height: 100%;
    padding: 5px 0px;
    margin: 0 18px;
    transition: all 0.3s;
    -o-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -webkit-transition: all 0.3s;
  }
  
  .main-menu > li > a::before {
    content: "";
    display: block;
    position: absolute;
    width: calc(100% + 36px);
    height: 5px;
    bottom: 0;
    left: -18px;
    background-color: #e6e6e6;
    transition: all 0.3s;
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    -moz-transition: all 0.3s;
  
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
  }
  
  .main-menu > li > a::after {
    content: "\F2F9";
    font-family: Material-Design-Iconic-Font;
    font-size: 16px;
    color: #222;
    line-height: 1.5;
    margin-left: 6px;
    margin-bottom: 1px;
    transition: all 0.3s;
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    -moz-transition: all 0.3s;
  }
  
  li.main-menu-active > a::before {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
  }
  
  .main-menu > li > a:hover:before {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
  }
  
  @media (max-width: 1199px) {
    .main-menu > li > a {
      margin: 0 10px;
    }
  
    .main-menu > li > a::before {
      left: -10px;
      width: calc(100% + 20px);
    }
  }
  
  /*---------------------------------------------*/
  .sub-menu {
    list-style-type: none;
    position: absolute;
    top:0;
    left:100%;
    width: 225px;
    background-color: #fff;
    padding: 15px 0px 15px 0px;
  
    transition: all 0.3s;
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    -moz-transition: all 0.3s;
  
    visibility: hidden;
    opacity: 0; 
    
    border: 1px solid #f2f2f2;
    box-shadow: 0 5px 10px 0px rgba(0,0,0,0.2);
